<template>
  <div class="section">
  <!-- 章节 -->
  <div class="section-head">
      <van-nav-bar title="章节" left-text="返回" left-arrow @click-left="back">
  <van-icon name="records" slot="right" size='20' color='black' />
</van-nav-bar>
  </div>
  <!-- 目录 -->
  <div class="section-list">
      <div class="list">
         <ul>
             <li v-for="(item,index) in list" :key="index" :id='item.id' @click="bannk({name: 'interfacered', params: {id: item.Id}})">
               <div class="fit-fl">
                     <span>{{item.name}}</span>
                </div>
                <div class="fit-fr">
                   <van-icon name="down" size='14' color='#D5D5D5' />
                </div>
             </li>
         </ul>
      </div>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        list:'',
        id:''
    };
  },
  computed: {},
  methods: {
      back(){
          this.$router.go(-1);
      },
      bannk(o){
          this.$router.push(o);
      }
  },
  mounted(){
   this.list=JSON.parse(localStorage.getItem('fotlist'));  
  },
  components: {},
  created() {
    this.list=JSON.parse(localStorage.getItem('fotlist'));
      //获取ID
      this.id = this.$route.params.id;
      if(!this.id){
        return
      }
    this.$axios({
      methods: "GET",
      dataType: "jsonp",
      url: "http://localhost:1000/getjson/xiaoshuo-"+this.id
    }).then(reche => {
       this.list=reche.data.data.list[0].list;
       localStorage.setItem('fotlist',JSON.stringify(reche.data.data.list[0].list));
    })
       
       
      
  },
};
</script>
<style lang="less">
 .section{
     position: fixed;
     left: 0;
     right: 0;
     top: 0;
     bottom: 0;
     .section-head{
         position: sticky;
         left: 0;
         top: 0;
         .van-nav-bar__text ,.van-icon{
             color: black;
         }
     }
     .section-list{
         width: 100%;
          overflow-y: auto;
           height: calc(100% - 46px);
         .list{
             width: calc(100% - 30px);
            
            overflow: hidden;
             padding: 0 15px;  
             ul{
                   overflow: hidden;
                 li{
                     width: 100%;
                     float: left;
                     border-bottom: 0.5px solid #f0f0f0;
                     padding:  15px 0px;
                      .fit-fl{
                  
                  overflow: hidden;
                  float: left;
                  span:nth-child(2){
                    margin-left: 15px;
                  }
                }
                .fit-fr{
                  float: right;
                }
                 }
             }
         }
     }
 }
</style>